<template>
<div>

    <button @click='showReg'></button>
    <button @click='showLog'></button>
    
  <div id="login" v-if='show'>
      <div class="login">
  
     <br>
     <input type="text">
     <br>

     <input type="text">
     <br>
     <button></button>
      </div>
  </div>


    <div class="registor" v-else>
     
     <br>
     
     <input type="text" v-model="tel">
     <br>
     <input type="text" v-model="userName">
     <br>

     <input type="text" v-model="LoginPassword">
    <br>
    <input type="text" v-model="msg">
     <button @click='getIphone'></button>
     <br>
     <button @click='registor'></button>
    </div>

    
  </div>
</template>

<script>
import qs from "qs"; // qs
//md5 
import md5 from "js-md5";
export default {
name:"Login",
data() {
return {
show:true,//||
tel:'', //
msg:'', //
msgtxt:'',//
userName:'',// 
LoginPassword:''//
}
},
// - this
methods:{
showLog(){
  this.show=true;
},
showReg(){
  this.show=false;
},
//
varifyTel(){
return this.$axios.get(`http://www.hj0819.top:44369/api/Verify/GetPhone`,{params:{phone:this.tel}})
},
//
varifyUser(){
return this.$axios.get(`http://www.hj0819.top:44369/api/Verify/LoginName`,{params:{LoginName:this.userName}})
},

//
async getIphone(){
   let tel=await this.varifyTel();

   let user=await this.varifyUser();



   console.log(tel.data);
   console.log(user)
   if (tel.data.code==200&&user.data.code==404){
     this.$axios.get(`http://www.hj0819.top:44369/api/SMSCode/GetCode?phone=${this.tel}&codeType=0`).then(res=>{
         console.log(res);

         this.msgtxt=res.data.msg;

     })
   }
   if(tel.data.code==204){
       console.log('')
   }
   if(user.data.code==200){
       console.log('')
   }
},
registor(){
    console.log(this.msg,'22'+this.msgtxt)
    if(this.msg==this.msgtxt){
        let params={LoginName:this.userName,LoginPassword:this.LoginPassword,phone:this.tel};
        params.LoginPassword=md5(params.LoginPassword);
   this.$axios.post(`http://www.hj0819.top:44369/api/LoginPage/AddLogin`,qs.stringify(params)).then(res=>{
        console.log(res);
    })
    }
    
}
},
created() {

},
// - DOM
mounted() {

}
}
</script>
